import React from "react";
import { Link } from "react-router-dom";
import { Panel, PanelHeader } from "../../../components/panel/panel.jsx";
import {
  ShowModalKondisiBarang,
  showModal,
  getDataKondisiBarang,
  hideModal,
} from "../../../actions/datamaster_action.jsx";
import { connect } from "react-redux";
import Swal from "sweetalert2";
import {
  NotifSucces,
  ToastNotification,
} from "../../../components/helpers/notification.jsx";
import { deleteData, postData, putData } from "../../../config/axios.jsx";
import { reset } from "redux-form";
import { ErrorHandling } from "../../../components/helpers/function.jsx";
import Tabel from "../../../components/helpers/tabel.jsx";
import ModalGlobal from "../../../components/helpers/ModalGlobal.jsx";
import FormModalKondisiBarang from "./FormModalKondisiBarang.jsx";
const maptostate = (state) => {
  return {
    DataKondisiBarang: state.datamaster.getDataKondisiBarang,
    hideModal: state.datamaster.modalDialog,
  };
};
const hapusDataKondisiBarang = (params, dispatch) => {
  Swal.fire({
    // title: "Anda Yakin !!",
    // text: "Ingin Menghapus Data Ini ?",
    html:
      "Apakah Anda Yakin Ingin " +
      "Menghapus " +
      "<h1><b>Kode Kondisi Barang " +
      params +
      "</b> ?</h1>",
    icon: "warning",
    position: "top-center",
    cancelButtonText: "Tidak",
    showCancelButton: true,
    confirmButtonText: "OK",
    showConfirmButton: true,
  }).then((result) => {
    if (result.isConfirmed) {
      deleteData("parakondisi/delete/by-kode-kondisi/" + params, {}).then(
        () => {
          NotifSucces("Data Berhasil Di Hapus").then(() =>
            dispatch(getDataKondisiBarang())
          );
        }
      );
    }
  });
};
class MasterKondisiBarang extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      isEdit: false,
      isLoading: false,
      modalDialog: false,
      columns: [
        {
          dataField: "kondisi_barang",
          text: "Kode Kondisi Barang",
          sort: true,
        },
        {
          dataField: "action",
          text: "Action",
          csvExport: false,
          headerClasses: "text-center",
          formatter: (rowcontent, row) => {
            let dataEdit = {
              kondisi_barang: row.kondisi_barang,
            };
             
            return (
              <div className="row text-center">
                <div className="col-12">
                  <button
                    onClick={() => this.formEditKondisiBarang(dataEdit)}
                    className="btn btn-primary mr-3"
                  >
                    <i className="fa fa-edit"></i>
                  </button>
                  <button
                    onClick={() =>
                      hapusDataKondisiBarang(
                        row.kondisi_barang,
                        this.props.dispatch
                      )
                    }
                    className="btn btn-danger"
                  >
                    <i className="fa fa-trash"></i>
                  </button>
                </div>
              </div>
            );
          },
        },
      ],
      // dataKondisiBarang: [
      //   {
      //     kondisi_barang: "BARANG RUSAK",
      //   },
      //   {
      //     kondisi_barang: "BARANG Hancur",
      //   },
      // ],
    };
    this.handleSubmit = this.handleSubmit.bind(this);
  }
  componentDidMount() {
    this.props.dispatch(getDataKondisiBarang());
  }
  handleSubmit(data) {
    this.setState({
      isLoading: true,
    });
    let data_edit = {
      kondisi_barang: data.kondisi_barang,
    };
    let data_post = {
      kondisi_barang: data.kondisi_barang,
    };
    this.state.isEdit
      ? putData(
          "parakondisi/edit/by-kode-kondisi/" + data.kondisi_barang_lama,
          data_edit
        )
          .then(() => {
            this.props.dispatch(hideModal());
          })
          .then(() => ToastNotification("success", "Data Berhasil Diedit"))
          .then(() => {
            this.props.dispatch(getDataKondisiBarang());
          })
          .then(() => {
            this.setState({ isLoading: false });
          })
          .then(() => {
            this.props.dispatch(reset("ModalKondisiBarang"));
          })
          .catch((error) => this.ErrorHandling(error))
      : postData("parakondisi/add", data_post)
          .then(() => {
            this.props.dispatch(hideModal());
          })
          .then(() => ToastNotification("success", "Data Berhasil Disimpan"))
          .then(() => {
            this.props.dispatch(getDataKondisiBarang());
          })
          .then(() => {
            this.props.dispatch(reset("ModalKondisiBarang"));
          })
          .then(() => {
            this.setState({ isLoading: false });
          })
          .catch((error) =>
            ErrorHandling(
              error,
              "parakondisi/reactive/by-kode-kondisi/",
              data.kondisi_barang,
              data_post,
              this.props.dispatch,
              getDataKondisiBarang(),
              hideModal(),
              "ModalKondisiBarang"
            ).then(() => {
              this.setState({ isLoading: false });
            })
          );
  }

  formEditKondisiBarang(dataEdit) {
    this.props.dispatch(showModal());
    this.props.dispatch(ShowModalKondisiBarang(dataEdit));
    this.setState({
      isEdit: true,
    });
  }
  ShowModalKondisiBarang() {
    this.props.dispatch(showModal());
    this.props.dispatch(ShowModalKondisiBarang(false));
    this.setState({
      isEdit: false,
    });
  }
  render() {
    return (
      <div>
        <ol className="breadcrumb float-xl-right">
          <li className="breadcrumb-item">
            <Link to="#">Data Master</Link>
          </li>
          <li className="breadcrumb-item active">Master Kondisi Barang</li>
        </ol>
        <h1 className="page-header">Master Kondisi Barang </h1>
        <Panel>
          <PanelHeader>Master Kondisi Barang</PanelHeader>
          <br />

          {/* Master Kondisi Barang */}
          <div className="container">
            <Tabel
              keyField="kondisi_barang"
              tambahData={true}
              handleClick={() => this.ShowModalKondisiBarang()}
              exportCsv={true}
              data={this.props.DataKondisiBarang}
              columns={this.state.columns}
              empty={this.props.DataKondisiBarang}
              textEmpty="Data Baki Kosong"
            />
          </div>
          <br />
          {/* End Master Sales */}
          <ModalGlobal
            size="P"
            title={
              this.state.isEdit
                ? "Edit Data Kondisi Barang"
                : "Tambah Data Kondisi Barang"
            }
            content={
              <FormModalKondisiBarang
                isEdit={this.state.isEdit}
                isLoading={this.state.isLoading}
                onSubmit={(data) => this.handleSubmit(data)}
              />
            }
          />

          {/* End Tambah Master Kondisi Barang  */}
        </Panel>
      </div>
    );
  }
}

export default connect(maptostate, null)(MasterKondisiBarang);
