import React, { Component } from "react";
import { connect } from "react-redux";
import { Field, reduxForm } from "redux-form";
import { hideModal } from "../../../actions/datamaster_action";
import { ReanderField } from "../../../components/helpers/field";
import ValidasiKondisiBarang from "../../../Validasi/ValidasiKondisiBarang.jsx";

const maptostate = (state) => {
  if (state.datamaster.ShowModalKondisiBarang !== null) {
    return {
      initialValues: {
        kondisi_barang_lama:
          state.datamaster.ShowModalKondisiBarang.kondisi_barang,
        kondisi_barang: state.datamaster.ShowModalKondisiBarang.kondisi_barang,
      },
    };
  }
};
class FormModalKondisiBarang extends Component {
  state = {
    aktif:false
  };

  componentDidMount() {
    setTimeout(() => {
      document.getElementById('kondisi_barang').focus()
    }, 100);
  }
  render() {
    return (
      <form onSubmit={this.props.handleSubmit} autoComplete="off">
        <input name="kondisi_barang_lama" type="hidden" />
        <Field
          tabIndex="1"
          id="kondisi_barang"
          name="kondisi_barang"
          component={ReanderField}
          type="text"
          label="Kode Kondisi Barang"
          onBlur={() => this.setState({ aktif: true })}
          placeholder="Masukan Kode Kondisi Barang"
        />
        <div className="row">
          <div className="col-12 text-right">
            <button
              className="btn btn-white"
              onClick={() => this.props.dispatch(hideModal())}
              type="button"
              disabled={this.props.isLoading}
            >
              Batal
            </button>
            &nbsp;
            <button
             tabIndex="2"
             className={this.state.aktif ? "btn btn-primary" : "btn btn-white"}
              disabled={this.props.submitting}
              type="submit"
            >
              {this.props.isLoading ? (
                <>
                  <i className="fas fa-spinner fa-spin"></i> &nbsp; Sedang
                  Menyimpan
                </>
              ) : (
                "Simpan Data"
              )}
            </button>
          </div>
        </div>
      </form>
    );
  }
}

FormModalKondisiBarang = reduxForm({
  form: "ModalKondisiBarang",
  enableReinitialize: true,
  validate: ValidasiKondisiBarang,
})(FormModalKondisiBarang);
export default connect(maptostate, null)(FormModalKondisiBarang);
